@use 'styles/colors';
@import 'styles/base';

:host {
  display: flex;

  .page-block {
    @extend %page-block;
    display: flex;
    align-items: center;
  }

  .hints-block {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
  }

  .hint-block {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-grow: 1;
    margin-bottom: 100px;

    &__title {
      @include proxima(30px, 700, colors.$foreground_dark);
      margin-bottom: 65px;
      max-width: 460px;
    }

    &__button {
      width: 320px;
    }

    &:first-child {
      align-items: flex-start;
    }

    &:last-child {
      align-items: flex-end;
      //margin-bottom: unset;

      .hint-block__title {
        margin-bottom: 30px;
        text-align: right;
        margin-left: auto;
      }
    }
  }

  @media screen and (max-width: 600px) {
    .hint-block {
      align-items: stretch;
      &__title {
        margin-bottom: 30px;
        text-align: center;
        max-width: unset;
      }

      &__button {
        width: 100%;
      }

      &:last-child {
        .hint-block__title {
          margin-bottom: 30px;
          text-align: center;
          margin-left: unset;
        }
      }
    }
  }
}


